/* ===============================================================================
Details CSS
=============================================================================== */
/* mainVisual
  ======================================== */
.banner{
  text-align: center;
  @include PC {margin-bottom: 100px;}
  @include SP {margin-bottom: 40px;}
  a{
    display: inline-block;
    &:hover{
      opacity: .7;
    }
  }
}
.posticon{
  position: absolute;
  @include PC {
    bottom: -43px;
    right: 60px;
  }
  @include SP {
    width: 23.12%;
    bottom: -43px;
    right: 2%;
  }
}
.mainVisual {
  background-color: #c85e58;
  @include PC {
    margin-top: -105px;
    padding-top: 105px;
  }
  @include SP {
    // margin-top: -105px;
    // padding-top: 105px;
  }
  > .inner {
    width: 984px;
    margin: 0 auto;
    position: relative;
  }
  .list {
    list-style: none;
    position: relative;
    height: 577px;
    li {
      position: absolute;
      z-index: -1;
      .img img {
        -webkit-transition-property: opacity;
        transition-property: opacity;
        -webkit-transition-duration: 2s;
        transition-duration: 2s;
        opacity: 0;
      }
      &.active .img img {
        -webkit-transition-duration: 1.8s;
        transition-duration: 1.8s;
        opacity: 1;
      }
    }
  }
  .logo {
    margin-top: -25px;
    text-align: center;
  }
  .coupon {
    position: absolute;
    top: 20px;
    left: 20px;
    opacity: 1;
    width: 192px;
    &:hover {
      opacity: 0.7;
    }
  }
}

@media screen and (max-width: 768px) {
  .mainVisual {
    // margin-bottom: 10px;
    > .inner {
      width: auto;
    }
    .coupon {
      position: absolute;
      top: 20px;
      left: 10px;
      opacity: 1;
      width: 90px;
      &:hover {
        opacity: 0.7;
      }
    }
    .list {
      height: auto;
      padding-top: 94.375%;
      li {
        top: 0;
        left: 0;
        width: 100%;
        .img img {
          width: 100%;
        }
      }
    }
    .logo {
      width: 78px;
      margin: -17px auto 0;
    }
  }
}

/* slideImg
  ======================================== */
.changeImage02{
  position: relative;
  @include PC {
    width: 430px;
    height: 525px;
  }
  @include SP {
    width: 80%;
  }
  li{
    position: absolute;
    top: 0;
    .img {
      top: 0;
      width: 430px;
      img {
        -webkit-transition-property: opacity;
        transition-property: opacity;
        -webkit-transition-duration: 2s;
        transition-duration: 2s;
        opacity: 0;
      }
    }
    &.active .img img {
      -webkit-transition-duration: 1.8s;
      transition-duration: 1.8s;
      opacity: 1;
    }
  }
}

/* sec1
  ======================================== */
.sec1 {
  background-color: #c75e58;
  text-align: center;
  color: #fff;
  @include PC {
    padding: 50px 0 80px;
  }
  @include SP {
    padding: 25px 0 40px;
  }
  h2{
    @include PC {margin-bottom: 50px;}
    @include SP {
      width: 51.56%;
      margin: auto;
      margin-bottom: 20px;
    }
  }
  h4{
    @include PC {
      font-size: 19px;
      margin-bottom: 15px;
    }
    @include SP {
      font-size: 10px;
      margin-bottom: 10px;
    }
  }
  .txt{
    @include PC {
      font-size: 18px;
      line-height: 2.2em;
      margin-bottom: 50px;
    }
    @include SP {
      font-size: 12px;
      line-height: 2em;
      margin-bottom: 25px;
    }
    strong{
      font-weight: bold;
      display: inline-block;
      @include PC {
        font-size: 24px;
        margin-bottom: 27px;
      }
      @include SP {
        font-size: 14px;
        line-height: 1em;
        margin-bottom: 25px;
      }
    }
  }
  .changeImage02{
    margin: auto;
    @include PC {
      height: 476px;
      width: 670px;
    }
    @include SP {
      height: auto;
      width: 100%;
      padding-top: 75%;
      li{width: 100%;}
    }
    .img{
      background-color: rgba(255,255,255,0.1);
      @include PC {
        width: 670px;
        padding: 18px 0;
      }
      @include SP {
        width: 100%;
        padding: 9px 0;
        img{
          width: 100%;
        }
      }
    }
  }
}

/* sec2
  ======================================== */
.sec2 {
  background-color: #ad3a33;
  overflow: hidden;
  @include PC {padding: 50px 0 80px;}
  @include SP {padding: 25px 0 40px;}
  h2{
    text-align: center;
    @include PC {margin-bottom: 50px;}
    @include SP {margin-bottom: 25px;}
    +p{
