        margin-bottom: 70px;
      }
      @include SP {
        font-size: 12px;
        margin-bottom: 35px;
      }
    }
    img{
     @include SP { width: 22%;}
    }
  }
  .imgWrap{
    margin: auto;
    @include PC {width: 945px;}
    @include SP {width: 93.7%;}
    .group{
      position: relative;
      color: #fff;
      @include PC {
        height: 569px;
        font-size: 18px;
      }
      @include SP {
        font-size: 12px;
        margin-bottom: 50px;
      }
      h3{ 
        position: absolute;
        z-index: 2;
      }
      .txt{
        position: absolute;
        z-index: 2;
        @include PC {font-size: 18px;}
        @include SP {font-size: 12px;}
        a{
          color: #fff;
          text-decoration: none;
          &:hover{
            opacity: .8;
          }
        }
      }
      .img{
        img{
          width: 100%;
        }
      }
      .changeImage02{
        @include SP {padding-top: 105%;}
        li{
          @include SP {width: 100%;}
        }
        .img{
          @include SP {
            position: relative;
            z-index: 1;
          }
        }
        &:after{
          content: "";
          display: block;
          width: 100%;
          background-color: #9d342e;
          @include PC {
            height: 569px;
          }
          @include SP {
            position: absolute;
            top: 2.5%;
            padding-top: 131%;
          }
        }
      }
      &.right{
        .changeImage02{
          @include PC {float: right;}
          @include SP {
            margin-left: 20%;
          }
          &:after{
            @include PC {
              margin-left: -15px;
              margin-top: 15px;
            }
            @include SP {
              right: 3%;
            }
          }
        }
        .txt{
          @include PC {text-align: right;}
        }
      }
      &.left{
        .changeImage02{
          @include PC {float: left;}
          &:after{
            @include PC {
              margin-left: 15px;
              margin-top: 15px;
            }
            @include SP {
              right: -3%;
            }
          }
        }
        .txt{
          text-align: left;
        }
      }
      &.wrap01{
        h3{
          @include PC {
            top: 80px;
            right: 410px;
          }
          @include SP {
            width: 18.83%;
            top: 60px;
            left: 9%;
          }
        }
        .txt{
          @include PC {
            margin-top: 220px;
            margin-left: 190px;
          }
          @include SP {
            bottom: 40px;
            left: 3%;
          }
        }
      }
      &.wrap02{
        @include PC {margin-top: -135px;}
        h3{
          @include PC {
            top: 220px;
            left: 390px;
          }
          @include SP {
            width: 47%;
            top: 70px;
            right: 0px;
          }
        }
        .txt{
          @include PC {
            top: 360px;
            left: 500px;
          }
          @include SP {
            bottom: 10px;
            right: 0;
          }
        }
      }
      &.wrap03{
        h3{
          @include PC {
            top: 150px;
            left: 300px;
          }
          @include SP {
            width: 47%;
            top: 110px;
            left: 0px;
          }
        }
        .txt{
          @include PC {
            top: 310px;
            left: 225px;
          }
          @include SP {
            bottom: 30px;
            left: 0;
          }
        }
      }
    }
    .changeImage02{
      @include PC {
        width: 434px;
        height: 569px;
      }
      .img{
        @include PC {width: 434px;}
        @include SP {width: 100%;}
      }
