  }
  .cartWrap{
    margin: auto;
    color: #fff;
    @include PC {
      width: 800px;
      margin-top: 70px;
      display: flex;
    }
    @include SP {
      width: 90%;
    }
    .cartInfo{
      text-align: center;
      @include PC {
        font-size: 18px;
        margin-right: 30px;
      }
