      text-align: center;
      transition-property: opacity;
      transition-duration: .2s;
      @include PC {
        padding: 17px 5px 14px;
        border: solid 2px #fff;
        @include hack(gteIE11) {
          padding: 14px 5px 10px;
        }
        &:hover {
