        float: left;
        width: 250px;
        margin-right: 18px;
      }
      @include SP {
        width: 56%;
        max-width: 250px;
      }
      a{
        span{
          line-height: 1;
          color: #fff;
          background:url(../img/common/common_contents_ico01_w.png) no-repeat left center;
          @include PC {
            font-size: 22px;
            background-size: 24px auto;
            padding-left: 33px;
            display: inline-block;
            @include hack(gteIE11) {
              padding-top: 8px;
            }
          }
          @include SP {
            font-size: 14px;
            background-size: 14px auto; 
            padding-left: 23px;
          }
        }
      }
    }
    .details{
      @include PC {
        float: left;
        width: 250px;
      }
      @include SP {
        width: 40%;
        max-width: 250px;
      }
      a{
        span {
          line-height: 1;
          color: #fff;
          background: url(../img/common/common_contents_arw03_w.png) no-repeat right center;
          background-size: 10px auto;
          padding-right: 19px;
          display: inline-block;
          @include PC {
            font-size: 22px; 
            @include hack(gteIE11) {
              padding-top: 8px;
            }
          }
          @include SP {
            font-size: 14px; 
          }
        }
      }
    }
  }
}

