/* modalBlock01
  ======================================== */

.modalBlock01 {
  .modalBox {
    width: 638px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 0.8);
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
    transform: translateY(-50%) translateX(-50%);
    z-index: -100;
    opacity: 0;
    padding: 67px 59px;
    -webkit-transition-property: opacity;
    transition-property: opacity;
    -webkit-transition-duration: .5s;
    transition-duration: .5s;
    .inner {
      position: relative;
    }
  }
  .closeBtn {
    height: 40px;
    width: 40px;
    display: block;
    position: absolute;
    right: -45px;
    top: -45px;
    cursor: pointer;
    &:before {
      content: '';
      height: 2px;
      width: 40px;
      display: block;
      background: #333;
      position: absolute;
      top: 15px;
      left: 1px;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
    }
    &:after {
      content: '';
      height: 2px;
      width: 40px;
      display: block;
      background: #333;
      position: absolute;
      top: 15px;
      left: 1px;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -o-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
    }
  }
  .closeWrap {
    display: none;
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  .slider {
    img {
      width: 100%;
    }
    .slick-arrow {
      position: fixed;
      z-index: 5;
      top: 50%;
      -webkit-transform: translateY(-50%);
      transform: translateY(-50%);
      cursor: pointer;
      width: 25px;
      height: 48px;
      font-size: 0;
      -webkit-transition-property: all;
      transition-property: all;
      -webkit-transition-duration: .2s;
      transition-duration: .2s;
      &:hover {
        opacity: .7;
      }
      &.slick-prev {
        left: 18px;
        background: url(../img/questionnaire/modal/questionnaire_modal_arw01.png) no-repeat 0 0;
        background-size: 100% auto;
      }
      &.slick-next {
        right: 18px;
        background: url(../img/questionnaire/modal/questionnaire_modal_arw02.png) no-repeat 0 0;
        background-size: 100% auto;
      }
    }
  }
}

